*{ margin: 0; padding: 0; }

:root{
    --widthHeightHomeButton: 130px;
    --corDosBotoes: rgb(85, 85, 85);
}

@-webkit-keyframes virarPlacaSelecao{
    from{
        top: -100px;
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }

    to{
        top: 0px;
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
        -webkit-transform: rotateX(360deg);
                transform: rotateX(360deg);
    }
}

@keyframes virarPlacaSelecao{
    from{
        top: -100px;
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }

    to{
        top: 0px;
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
        -webkit-transform: rotateX(360deg);
                transform: rotateX(360deg);
    }
}

#botaoTelaSelecao{
    position: absolute;
    left: 85%;
    top: 0%;
    width: 300px;
    height: 100px;
    background-color: rgb(255, 185, 138);
    background-image: url(/gestalt-laws-game-deploy/static/media/botaoSelecao.86009710.svg);
    background-repeat: no-repeat;
    background-color: transparent;
    z-index: 120;

    -webkit-animation-name: virarPlacaSelecao;

            animation-name: virarPlacaSelecao;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    /*animation-delay: 3s; */
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    /*animation-direction: reverse;*/
}

