@import url('https://fonts.googleapis.com/css2?family=Nova+Oval&display=swap');

:root{
    --corDosBotoes: rgb(85, 85, 85);
    --leftPlacas: 33.2%;
}

h3{ 
    color: rgb(61, 42, 5);
    font-size: 390%;
    font-family: 'Nova Oval', cursive; 
    text-align: center; 
    line-height: 2.1;
    /*
    text-shadow: 3px 3px 2px rgb(70, 45, 25), -3px -3px 2px rgb(59, 34, 15);
    */
}

.placaTorreRelogio:hover{
    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease;
    transform: rotate(30deg);
}
.placaLaboratorio:hover{
    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease;
    transform: rotate(-10deg);
}
.placaMoinho:hover{
    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease;
    transform: rotate(-35deg);
}





.placaTorreRelogio h3{ 
    position: absolute;
    font-size: 330%;
    left: 11%;
}
.placaTorreRelogio{ /*primeira de cima*/
    position: absolute;
    width: 478px;
    height: 120px;
    left: 32.2%;
    top: 15%;
    z-index: 100;
    background-image: url('../images/telaSelecao/placa1.svg');
    background-repeat: no-repeat;
}



.placaLaboratorio{/*placa do meio*/
    position: absolute;
    width: 479px;
    height: 120px;
    left: 26.2%;
    top: 40%;
    z-index: 90;
    background-image: url('../images/telaSelecao/placa2.svg');
    background-repeat: no-repeat;
}



.placaMoinho{
    position: absolute;
    width: 478px;
    height: 120px;
    left: 33.2%;
    top: 65%;
    z-index: 80;
    background-image: url('../images/telaSelecao/placa3.svg');
    background-repeat: no-repeat;
}

.poste{
    position: absolute;
    width: 800px;
    height: 800px;
    left: 17%;
    top: -10%;
    z-index: 70;
    background-image: url('../images/telaSelecao/poste.svg');
    background-repeat: no-repeat;
}






#botaoRank{
    position: absolute;
    left: 77%;
    top: 74%;
    width: 250px;
    height: 90px;
    cursor: pointer;
    z-index: 92;
}

#botaoRank, #coroaIMG{
    position: absolute;
    background-image: url('../images/telaSelecao/coroa.svg');
    background-repeat: no-repeat;
    z-index: 100;
}




#plaquinhaRank{/*placa abaixo da palabra "ranking"*/
    position: absolute;
    left: 76%;
    top: 88%;
    width: 270px;
    height: 67.8px;
    background-image: url('../images/telaSelecao/placa2.svg');
    background-repeat: no-repeat;
    background-color: rgb(0, 0, 0, 0.3);
    z-index: 100;
}

#plaquinhaRank h3{/* TEXTO "RANKING" */
    font-size: 270%;
    z-index: 101;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}




